.list {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    padding: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: baseline;

    .item {
        border: 1px solid rgba(0,0,0, 0.1);
        box-shadow: 0 2px 4px 0 #D1D6E6, 0 12px 32px 0 rgba(209,214,230,0.5);
        cursor: pointer;
        width: 100%;  // Default to full width
        padding: 0 0 10px 10px;
        overflow: hidden; // Or flex might break
        list-style: none;
        width: calc(94%/4);
        flex-basis: calc(94%/4);
        margin-bottom: 20px;
        margin-right: 2%;

        @media(max-width: 768px) and (min-width: 600px) {
            flex-basis: calc(94%/3);
            margin-right: 3%;
            
            &:nth-child(3n + 3) {
                margin-right: 0;
            }
        }
        
        @media(max-width: 600px) {
            flex-basis: calc(96%/2);
            margin-right: 2%;

            &:nth-child(2n + 2) {
                margin-right: 0;
            }
        }

        @media(min-width: 768px) {   
            &:nth-child(4n + 4) {
                margin-right: 0;
            }
        }

        p {
            color: #121212;
        }

        .text {
            float: right;
            width: 70%;
            padding-right: 8px;

            @media(max-width: 600px) {
                width: 100%;
            }

            p {
                text-transform: capitalize;
                font-size: 18px;
                font-weight: bold;

                &.name {
                    font-size: 20px;
                    margin: 8px 0 5px;
                }

                &.eng {
                    font-size: 16px;
                    margin: 0;
                }
            }
        }

        .image {
            float: left;
            width: 30%;

            @media(max-width: 600px) {
                width: 100%;
            }
        }
    }
}

.modal-content {
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;

    span {
        display: flex;
        line-height: 48px;
        font-size: 28px;
        color: white;
        background: #3880ff;
        border-radius: 10px;
        width: 90%;
        margin-bottom: 15px;
        text-align: center;
        justify-content: center;
    }
}